.results-container {
	max-width: 35rem;
	margin: 2rem auto;
	padding: 1rem;
	grid-area: results;
	text-align: left;

	.query-message {
		text-align: center;
		color: #90b1aa;
		.query {
			font-size: 20px;
			color: whitesmoke;
		}
	}

	ul {
		list-style-type: none;
		padding-left: 0;
	}
}
